@import url(https://fonts.googleapis.com/css?family=Gamja+Flower);
@import url(https://fonts.googleapis.com/css?family=Gamja+Flower);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: 'Gamja Flower',cursive;  
}

.Title {
    display:flex;
    justify-content: center;
    align-items: center;
    height: 15em;
    background-color: #9ff9cf8c;
}

h1 {
    font-size:4em;
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 25vh;
    background-color: #FFD7A38C;

}

button,
input {
    width: 60%;
    margin-bottom: 10px;
}

input {
    height: 30px;
}

input[type=text]:focus {
    border: 3px solid #9FF9D0;
}

button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 3px;
}
.weather-display {
        display: flex;
        justify-content: center;
        height: 100vh;
        background-color: #FFD7A38C;
}
body {
  font-family: 'Gamja Flower', cursive;
}
